import React from 'react'
import addCss from './Add.module.css'

const Add = props => {

    let newTodoTitleValue = ''
    const myRef = React.createRef()

    const writeNewTodoTitle = (event) => {
        newTodoTitleValue = event.nativeEvent.target.value.toString().trim()
    }

    const clickHandler = () => {
        props.addTodoHandler(newTodoTitleValue)
        newTodoTitleValue = ''
        myRef.current.value = ''
    }

    return (
        <div
            className={addCss.add}
        >
            <input
                type="text"
                placeholder={'Task'}
                onChange={writeNewTodoTitle}
                ref={myRef}
            />
            <button
                onClick={clickHandler}
            >ADD
            </button>
        </div>
    )
}

export default Add
