import React, {useContext, useEffect} from 'react'
import loginCss from './Auth.module.css'
import {isAuth, login, register} from '../modules/login'
import Login from "./Login/Login"
import Register from "./Register/Register"
import {AuthContext} from "../context/Auth/AuthContext";

const Auth = props => {

    const auth = useContext(AuthContext)

    useEffect(() => {
        if (isAuth()) {
            props.history.push({
                pathname: '/todos'
            })
        }
    }, [])

    const changeActiveForm = (type) => {
        auth.changeFormType(type)
    }

    const loginHandler = (values) => {
        if (!values) return

        const user = {
            email: values.email.toString().trim(),
            password: values.password.toString().trim()
        }

        const status = login(user)

        if (status) {
            props.history.push({
                pathname: '/todos'
            })
        } else {
            alert('User not found!')
        }

    }

    const registerHandler = (values) => {
        if (!values) return

        const newUser = {
            email: values.email.toString().trim(),
            password: values.password.toString().trim(),
            passwordConfirm: values.passwordConfirm.toString().trim(),
        }

        const status = register(newUser)

        if (status) props.history.push({
            pathname: '/todos'
        })
    }

    const legend = (
        <legend>
                     <span
                         className={auth.activeForm === 'login' ? loginCss.active : null}
                         onClick={changeActiveForm.bind(this, 'login')}
                     >Login</span>
            <span
                className={auth.activeForm === 'reg' ? loginCss.active : null}
                onClick={changeActiveForm.bind(this, 'reg')}
            >Register</span>
        </legend>
    )

    return (
        <div className={loginCss.LoginContainer}>
            <div className={loginCss.Login}>
                {
                    auth.activeForm === 'login'
                        ? <Login legend={legend} loginHandler={loginHandler}/>
                        : <Register legend={legend} registerHandler={registerHandler}/>
                }
            </div>
        </div>
    )

}

export default Auth
