import {TodosContext} from "./TodosContext";
import React, {useReducer} from "react";
import {TodosReducer} from "./TodosReducer";
import {CLOSE_MODAL, DELETE_MODAL, EDIT_MODAL, SET_TODOS, SORT} from "../actionTypes";
import {getAllTodos, saveTodos} from "../../modules/todo";

export const TodosState = props => {

    const initialState = {
        todos: [],
        modal: {
            isShow: false,
            type: '',
            deleteId: null,
            editId: null,
            editText: ''
        },
        sort: {
            priority: true,
            date: false,
            completed: false
        }
    }

    const [state, dispatch] = useReducer(TodosReducer, initialState)

    const getTodos = () => {
        getAllTodos().then(res => {
            const arr = res.map(el => {
                return {
                    ...el,
                    date: el.date || Date.now(),
                    priority: el.priority || el.userId,
                    hide: false
                }
            })

            saveTodos(arr)

            setTodos(arr)
        })
    }

    const setTodos = todos => {
        dispatch({
            type: SET_TODOS,
            payload: todos
        })
    }

    const closeTodoModal = () => {
        dispatch({
            type: CLOSE_MODAL,
            payload: {
                isShow: false,
                type: '',
                deleteId: null,
                editId: null,
                editText: ''
            }
        })
    }

    const showDeleteTodoModal = (newModal) => {
        dispatch({
            type: DELETE_MODAL,
            payload: newModal
        })
    }

    const showEditTodoModal = (newModal) => {
        dispatch({
            type: EDIT_MODAL,
            payload: newModal
        })
    }

    const changeSort = newSortObj => {
        dispatch({
            type: SORT,
            payload: newSortObj
        })
    }

    const {todos, modal, sort} = state

    return (
        <TodosContext.Provider value={{
            todos, sort, modal, setTodos, getTodos, closeTodoModal,
            showDeleteTodoModal, showEditTodoModal, changeSort
        }}>
            {props.children}
        </TodosContext.Provider>
    )
}
