import React, {useContext, useEffect, useState} from 'react'
import todosCss from './Todos.module.css'

import Todo from "./Todo/Todo";
import Filters from './Filters/Filters'
import Add from './Add/Add'
import Rebuild from "../Rebuild/Rebuild";

import {saveTodos} from '../modules/todo'
import {isAuth} from '../modules/login'
import TodoModal from "./TodoModal/TodoModal";
import {TodosContext} from "../context/Todos/TodosContext";

const Todos = props => {

    const todosContext = useContext(TodosContext)

    let searchToggle = true
    const minLength = 5
    const maxLength = 100

    useEffect(() => {
        if (!isAuth()) {
            props.history.push({
                pathname: '/'
            })

            return
        }

        todosContext.getTodos()
    }, [])

    const closeAlert = () => {
        todosContext.closeTodoModal()
    }

    const showDeleteAlert = (id) => {
        if (!id) return

        todosContext.showDeleteTodoModal({
            ...todosContext.modal,
            isShow: true,
            type: 'delete',
            deleteId: id
        })
    }

    const showEditAlert = (id, title) => {
        if (!id || !title) return

        todosContext.showEditTodoModal({
            ...todosContext.modal,
            isShow: true,
            type: 'edit',
            editId: id,
            editText: title
        })
    }

    const changePriority = (id, num) => {
        if (!id || !num) return

        const arr = todosContext.todos.map(el => {
            if (el.id === id) {
                const result = el.priority + num
                el.priority = result > 1 ? result : 1
            }
            return el
        })

        if (arr.length) {
            saveAllTodosInStorage(arr)

            todosContext.setTodos(arr)
        }
    }

    const deleteTodo = (id) => {
        if (!id) return

        const arr = todosContext.todos.filter(el => el.id !== id)

        saveAllTodosInStorage(arr)

        todosContext.setTodos(arr)

        closeAlert()
    }

    const editTodo = (id, title) => {
        if (!id || !title) return

        const arr = todosContext.todos.map(el => {
            if (el.id === id) el.title = title

            return el
        })

        if (arr.length) {
            saveAllTodosInStorage(arr)

            todosContext.setTodos(arr)
        }

        closeAlert()
    }

    const checkedTodo = (id) => {
        if (!id) return

        // eslint-disable-next-line array-callback-return
        const arr = todosContext.todos.map(el => {
            if (el.id === id) el.completed = !el.completed
            return el
        })

        if (arr.length) {
            saveAllTodosInStorage(arr)

            todosContext.setTodos(arr)
        }
    }

    const sortTodos = (key) => {
        let arr = []

        if (todosContext.sort[key]) {
            arr = todosContext.todos.sort((a, b) => +a[key] < +b[key] ? 1 : -1)
        } else {
            arr = todosContext.todos.sort((a, b) => +a[key] > +b[key] ? 1 : -1)
        }

        if (arr.length) {

            todosContext.setTodos(arr)

            todosContext.changeSort({
                ...todosContext.sort,
                [key]: !todosContext.sort[key]
            })
        }
    }

    const sortByPriority = () => {
        const KEY = 'priority'
        sortTodos(KEY)
    }

    const sortByDate = () => {
        const KEY = 'date'
        sortTodos(KEY)
    }

    const sortByChecked = () => {
        const KEY = 'completed'
        sortTodos(KEY)
    }

    const searchHandler = (event) => {
        const TIMEOUT = 1000

        if (searchToggle) {
            searchToggle = false

            setTimeout(() => {
                const value = event.nativeEvent.target.value.toString().trim()
                searchAfterTimeout(value)

                searchToggle = true
            }, TIMEOUT)
        }
    }

    const searchAfterTimeout = (value) => {
        let arr = []

        if (value) {
            arr = todosContext.todos.filter(el => {
                el.hide = !el.title.includes(value)
                return el
            })
        } else {
            arr = todosContext.todos.filter(el => {
                el.hide = false
                return el
            })
        }

        if (arr.length) {
            todosContext.setTodos(arr)
        }
    }

    const addNewTodo = (value) => {
        const str = value.toString().trim().length

        if (str < minLength || str > maxLength) {
            alert(`Invalid string length! The correct length is ${minLength} to ${maxLength} characters.`)
            return
        }

        let maxId = 0

        todosContext.todos.forEach(el => {
            if (el.id > maxId) maxId = el.id
        })

        const todo = {
            completed: false,
            date: Date.now(),
            hide: false,
            priority: 1,
            id: maxId + 1,
            title: value.toString().trim()
        }

        const arr = [todo, ...todosContext.todos]

        if (arr.length) {
            saveAllTodosInStorage(arr)

            todosContext.setTodos(arr)
        }

    }

    const saveAllTodosInStorage = async todos => {
        const newTodos = todos.map(el => {
            el.hide = false
            return el
        })

        if (newTodos.length) {
            saveTodos(newTodos)
        }
    }


    return (
        <React.Fragment>
            {
                todosContext.modal.isShow
                    ? <TodoModal
                        modalState={todosContext.modal}
                        deleteHandler={deleteTodo}
                        editHandler={editTodo}
                        closeHandler={closeAlert}
                    />
                    : null
            }

            <div className={todosCss.mainContainer}>
                <h1 className={todosCss.title}>TODO LIST</h1>
                <Add
                    addTodoHandler={addNewTodo}
                />
                <Filters
                    prioritySortHandler={sortByPriority}
                    dateSortHandler={sortByDate}
                    checkedSortHandler={sortByChecked}
                    searchHandler={searchHandler}
                />
                <Rebuild
                    history={props.history}
                />
                <div className={todosCss.Todos}>
                    {todosContext.todos.length ?
                        todosContext.todos.map(el => {
                            return (
                                <Todo
                                    key={el.id}
                                    title={el.title}
                                    completed={el.completed}
                                    priority={el.priority}
                                    deleteHandler={showDeleteAlert.bind(this, el.id)}
                                    editHandler={showEditAlert.bind(this, el.id, el.title)}
                                    checkHandler={checkedTodo.bind(this, el.id)}
                                    upPriorityHandler={changePriority.bind(this, el.id, 1)}
                                    downPriorityHandler={changePriority.bind(this, el.id, -1)}
                                    hide={el.hide}
                                    date={el.date}
                                />
                            )
                        })
                        : (
                            <div className={todosCss.loader}>
                                <p>Loading...</p>
                            </div>
                        )
                    }
                </div>
            </div>
        </React.Fragment>
    )
}

export default Todos
