import React from 'react'
import registerCss from './Register.module.css'
import * as yup from "yup";
import {Formik} from "formik";

const Register = props => {

    const renderLoginForm = () => {
        const validationSchema = yup.object().shape({
            email: yup.string().required('Введите email').email('Email не корректный')
                .min(5, 'Email должен быть больше 5 символов')
                .max(40, 'Email должен быть не больше 40 символов'),
            password: yup.string().required('Введите пароль')
                .min(5, 'Пароль должен быть больше 5 символов')
                .max(16, 'Пароль должен быть не больше 16 символов'),
            passwordConfirm: yup.string().required('Введите пароль')
                .min(5, 'Пароль должен быть больше 5 символов')
                .max(16, 'Пароль должен быть не больше 16 символов')
                .oneOf([yup.ref('password')], 'Пароли не совпадают')
        })

        return (
            <Formik
                initialValues={{
                    email: '',
                    password: '',
                    passwordConfirm: ''
                }}
                validateOnBlur
                onSubmit={values => props.registerHandler(values)}
                validationSchema={validationSchema}
            >
                {
                    ({
                         values, errors, touched,
                         handleChange, handleBlur,
                         isValid, handleSubmit, dirty
                     }) => (
                        <form className={registerCss.form}>
                            {props.legend}
                            <div className={registerCss.formItem}>
                                <label htmlFor="email">Email</label>
                                <input
                                    type="email"
                                    name={'email'}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.name}
                                    className={touched.email && errors.email ? registerCss.invalid : registerCss.valid}
                                />
                                {touched.email && errors.email &&
                                <span className={registerCss.error}>{errors.email}</span>}
                            </div>
                            <div className={registerCss.formItem}>
                                <label htmlFor="password">Password</label>
                                <input
                                    defaultValue={''}
                                    type="password"
                                    name={'password'}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.name}
                                    className={touched.password && errors.password ? registerCss.invalid : registerCss.valid}
                                    autoComplete={'on'}
                                />
                                {touched.password && errors.password &&
                                <span className={registerCss.error}>{errors.password}</span>}
                            </div>
                            <div className={registerCss.formItem}>
                                <label htmlFor="passwordConfirm">Confirm password</label>
                                <input
                                    defaultValue={''}
                                    type="password"
                                    name={'passwordConfirm'}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.name}
                                    className={touched.passwordConfirm && errors.passwordConfirm ? registerCss.invalid : registerCss.valid}
                                    autoComplete={'on'}
                                />
                                {touched.passwordConfirm && errors.passwordConfirm &&
                                <span className={registerCss.error}>{errors.passwordConfirm}</span>}
                            </div>
                            <button
                                className={registerCss.btn + ` ${registerCss.btnRegister}`}
                                disabled={!isValid || !dirty}
                                onClick={handleSubmit}
                                type={'submit'}
                            >Отправить
                            </button>
                        </form>
                    )
                }
            </Formik>
        )
    }


    return renderLoginForm()

}

export default Register
