import React from 'react'
import rebuildCss from './Rebuild.module.css'
import {removeUserAuth} from '../modules/login'
import {clearTodos} from '../modules/todo'

const Rebuild = props => {

    const rebuild = () => {
        removeUserAuth()
        clearTodos()
        props.history.push({
            pathname: '/'
        })
    }

    return (
        <button
            className={rebuildCss.Rebuild}
            onClick={rebuild}
        >REBUILD</button>
    )
}

export default Rebuild
