import {CLOSE_MODAL, DELETE_MODAL, EDIT_MODAL, SET_TODOS, SORT} from "../actionTypes";

export const TodosReducer = (state, action) => {

    switch (action.type) {
        case SET_TODOS:
            return {
                ...state,
                todos: action.payload
            }
        case CLOSE_MODAL:
            return {
                ...state,
                modal: {...action.payload}
            }
        case DELETE_MODAL:
            return {
                ...state,
                modal: {...action.payload}
            }
        case EDIT_MODAL:
            return {
                ...state,
                modal: {...action.payload}
            }
        case SORT:
            return {
                ...state,
                sort: {...action.payload}
            }
        default:
            return state
    }
}
