import axios from "axios"

const storage = window.localStorage
const KEY_TODOS = 'todos_5'

export function clearTodos() {
    storage.removeItem(KEY_TODOS)
}

export async function getAllTodos() {
    return new Promise(resolve => {
        let todos = getStorageTodos()

        if (todos instanceof Array && todos.length) {
            console.log('Storage Todos')
            resolve(todos)
        } else {
            getServerTodos().then(res => {
                console.log('Server Todos')
                saveTodos(res)
                resolve(res)
            })
        }
    })
}

async function getServerTodos() {
    const url = 'https://jsonplaceholder.typicode.com/todos'

    try {
        const response = await axios.get(url)

        return response.data
    } catch (e) {
        console.log(e)
        return []
    }
}

function getStorageTodos() {
    const todos = JSON.parse(storage.getItem(KEY_TODOS))

    if (todos instanceof Array) {
        return todos
    }

    return []
}

export async function saveTodos(todos) {
    return new Promise(resolve => {
        if (todos instanceof Array && todos.length) {
            storage.setItem(KEY_TODOS, JSON.stringify(todos))
            console.log('Todos were saved!')
            resolve()
        }
    })
}
