import React from 'react'
import loginCss from './Login.module.css'
import * as yup from "yup";
import {Formik} from "formik";

const Login = props => {

    const renderLoginForm = () => {
        const validationSchema = yup.object().shape({
            email: yup.string().required('Введите email').email('Email не корректный')
                .min(5, 'Email должен быть больше 5 символов')
                .max(40, 'Email должен быть не больше 40 символов'),
            password: yup.string().required('Введите пароль')
                .min(5, 'Пароль должен быть больше 5 символов')
                .max(16, 'Пароль должен быть не больше 16 символов')
        })

        return (
            <Formik
                initialValues={{
                    email: '',
                    password: ''
                }}
                validateOnBlur
                onSubmit={values => props.loginHandler(values)}
                validationSchema={validationSchema}
            >
                {
                    ({
                         values, errors, touched,
                         handleChange, handleBlur,
                         isValid, handleSubmit, dirty
                     }) => (
                        <form className={loginCss.form}>
                            {props.legend}
                            <div className={loginCss.formItem}>
                                <label htmlFor="email">Email</label>
                                <input
                                    type="email"
                                    name={'email'}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.name}
                                    className={touched.email && errors.email ? loginCss.invalid : loginCss.valid}
                                />
                                {touched.email && errors.email &&
                                <span className={loginCss.error}>{errors.email}</span>}
                            </div>
                            <div className={loginCss.formItem}>
                                <label htmlFor="password">Password</label>
                                <input
                                    defaultValue={''}
                                    type="password"
                                    name={'password'}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    value={values.name}
                                    className={touched.password && errors.password ? loginCss.invalid : loginCss.valid}
                                    autoComplete={'on'}
                                />
                                {touched.password && errors.password &&
                                <span className={loginCss.error}>{errors.password}</span>}
                            </div>
                            <button
                                className={loginCss.btn + ` ${loginCss.btnLogin}`}
                                disabled={!isValid || !dirty}
                                onClick={handleSubmit}
                                type={'submit'}
                            >Отправить
                            </button>
                        </form>
                    )
                }
            </Formik>
        )
    }

    return renderLoginForm()
}

export default Login
