const storage = window.localStorage
const KEY_USERS = 'users_5'
const KEY_LOGIN = 'is_login_5'

function getUsers() {
    const data = JSON.parse(storage.getItem(KEY_USERS))
    return data instanceof Array ? data : null
}

function addUsers(users) {
    if (users instanceof Array) {
        storage.setItem(KEY_USERS, JSON.stringify(users))
    }
}

function saveUser() {
    storage.setItem(KEY_LOGIN, 'true')
}

export function removeUserAuth() {
    storage.removeItem(KEY_LOGIN)
}

export function isAuth() {
    return storage.getItem(KEY_LOGIN) === 'true'
}

export function login(user) {
    if (user.email && user.password) {
        const users = getUsers()

        if (users instanceof Array) {
            const currentUser = users.filter(el => {
                if (el.email === user.email && el.password === user.password) return el
            })

            saveUser()
            return !!currentUser.length
        }

        return false
    }

    return false
}

export function register(user) {
    if (user.email && user.password && user.passwordConfirm) {
        if (user.password === user.passwordConfirm) {
            const users = getUsers() || []

            // eslint-disable-next-line array-callback-return
            const check = users.filter(el => {
                if (el.email === user.email) return el
            })

            if (check.length) {
                alert('This user already exists!')
                return
            }

            users.push({
                email: user.email,
                password: user.password
            })

            addUsers(users)
            saveUser()

            return true
        }

        return false
    }

    return false
}
