import React, {useEffect} from 'react'
import modalCss from './TodoModal.module.css'

const TodoModal = props => {

    const myRef = React.createRef()
    const maxLength = 100
    const minLength = 5

    let editTextValue = ''

    useEffect(() => {
        if (props.modalState.type === 'edit') {
            myRef.current.innerHTML = maxLength - props.modalState.editText.toString().trim().length
            editTextValue = props.modalState.editText.toString().trim()
        }
    }, [])

    const changeTextHandler = (event) => {
        const value = event.nativeEvent.target.value.toString().trim()

        editTextValue = value
        myRef.current.innerHTML = maxLength - value.length
    }

    const deleteTodo = () => {
        props.deleteHandler(props.modalState.deleteId)
    }

    const edit = () => {
        const length = editTextValue.length

        if (length < minLength || length > maxLength) {
            alert(`Invalid string length! The correct length is ${minLength} to ${maxLength} characters.`)
            return
        }

        if (props.modalState.editText.toString().trim().length !== editTextValue.length) {
            props.editHandler(props.modalState.editId, editTextValue)
        }
    }

    return (
        <React.Fragment>
            {
                props.modalState.isShow && props.modalState.type === 'delete'
                    ? <div className={modalCss.container}>
                        <div className={modalCss.delete}>
                            <h2 className={modalCss.delete__title}>Delete item?</h2>
                            <div className={modalCss.delete__buttons}>
                                <button
                                    className={modalCss.delete__button}
                                    onClick={deleteTodo}
                                >YES
                                </button>
                                <button
                                    className={`${modalCss.delete__button} ${modalCss.delete__buttons_no}`}
                                    onClick={props.closeHandler}
                                >NO
                                </button>
                            </div>
                        </div>
                    </div>
                    : null
            }

            {
                props.modalState.isShow && props.modalState.type === 'edit'
                    ? <div className={modalCss.container}>
                        <div className={modalCss.edit}>
                            <h2 className={modalCss.edit__title}>Edit text</h2>
                            <textarea
                                className={modalCss.edit__area}
                                defaultValue={props.modalState.editText}
                                onChange={changeTextHandler}
                            />
                            <span>
                                Characters limit:
                                    <p className={modalCss.edit__count} ref={myRef}/>
                            </span>
                            <div className={modalCss.edit__buttons}>
                                <button
                                    className={modalCss.edit__button + ' ' + modalCss.edit__button_cancel}
                                    onClick={props.closeHandler}
                                >CANCEL
                                </button>
                                <button
                                    className={modalCss.edit__button}
                                    onClick={edit}
                                >SAVE
                                </button>
                            </div>
                        </div>
                    </div>
                    : null
            }
        </React.Fragment>
    )
}

export default TodoModal
